/**
 * #.# Styles
 *
 * CSS for both Frontend+Backend.
 */
.eb-instagram-wrapper {
  margin: 0 auto; }
  @media all and (max-width: 375px) {
    .eb-instagram-wrapper .eb-instagram-grid {
      grid-template-columns: repeat(1, 1fr) !important; } }
  @media all and (min-width: 375px) and (max-width: 540px) {
    .eb-instagram-wrapper .eb-instagram-grid {
      grid-template-columns: repeat(2, 1fr) !important; } }
  @media all and (min-width: 540px) and (max-width: 786px) {
    .eb-instagram-wrapper .eb-instagram-grid {
      grid-template-columns: repeat(4, 1fr) !important; } }
  .eb-instagram-wrapper .eb-instagram-grid.use-thumbnail {
    grid-auto-rows: min-content; }

.display-grid {
  display: grid; }

/* flexbox fallback if the browser does not support display:grid */
@supports not (display: grid) {
  .display-grid {
    display: flex;
    flex-wrap: row wrap; }
    .display-grid > * {
      flex: 0 1 1; } }

/**
Image Styling
**/
.eb-instagram-image-wrapper {
  position: relative; }
  .eb-instagram-image-wrapper:hover .eb-instagram-image-overlay {
    opacity: 1; }
  .eb-instagram-image-wrapper:hover img {
    opacity: 0.6; }
  .eb-instagram-image-wrapper.has-equal-images {
    overflow: hidden;
    padding-bottom: 100%; }
    .eb-instagram-image-wrapper.has-equal-images img {
      position: absolute;
      object-fit: cover;
      width: 100%;
      height: 100%; }

.eb-instagram-image {
  display: block;
  margin: 0 auto; }

.eb-instagram-image-overlay {
  transition: all 0.25s ease-in;
  position: absolute;
  background-color: transparent;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0; }
  .eb-instagram-image-overlay:before {
    content: " ";
    opacity: 1;
    background: url(./img/instagram-icon-white.svg) no-repeat center center;
    background-size: 30px 30px;
    position: absolute;
    z-index: 100;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0; }

/**
Profile pictures
**/
.eb-instagram-profile-container {
  display: block;
  margin-bottom: 1rem; }
  @media all and (min-width: 541px) {
    .eb-instagram-profile-container {
      display: grid;
      grid-template-columns: 60px auto;
      grid-column-gap: 1rem; } }

.eb-instagram-profile-picture {
  border-radius: 100%; }

.eb-instagram-bio-container h3,
.eb-instagram-bio-container p {
  margin-top: 0;
  margin-bottom: 0; }

/**
Captions
**/
.eb-instagram-image-caption {
  position: absolute;
  bottom: 0;
  padding: 10px;
  color: #fff;
  font-size: 13px; }
  .eb-instagram-image-caption .eb-instagram-image-caption_text {
    display: -webkit-box;
    -webkit-line-clamp: 3;
    overflow: hidden;
    /*! autoprefixer: off */
    -webkit-box-orient: vertical;
    /* autoprefixer: on */ }
